import React, { Component } from 'react';
import { BrowserRouter as Router, Switch } from 'react-router-dom';
/*------ Pages-----*/
import PaymentProcessing from './Pages/Payment-processing';
import Bloglist from './Pages/Bloglist';
import BlogSingle from './Pages/BlogSingle';
import Contact from './Pages/Contact';
import ScrollToTopRoute from './ScrollToTopRoute';
import Faq from './Pages/Faq';
import SignIn from './Pages/SignIn';
import SignUp from './Pages/SignUp';
import BlogGridPage from './Pages/BlogGridPage';
import NotFound from './Pages/404';

class App extends Component {
   componentDidMount() {
      this.props.hideLoader();
   }
   render() {
      return (
         <Router>
            <Switch>
               <ScrollToTopRoute exact={true} path={'/ex-pay'} component={PaymentProcessing} />
               <ScrollToTopRoute path="/ex-pay/Contact" component={Contact} />
               <ScrollToTopRoute path='/ex-pay/Bloglist' component={Bloglist} />
               <ScrollToTopRoute path='/ex-pay/BlogSingle' component={BlogSingle} />
               <ScrollToTopRoute path='/ex-pay/BlogGridPage' component={BlogGridPage} />
               <ScrollToTopRoute path='/ex-pay/Faq' component={Faq} />
               <ScrollToTopRoute path='/ex-pay/SignIn' component={SignIn} />
               <ScrollToTopRoute path='/ex-pay/SignUp' component={SignUp} />
               <ScrollToTopRoute component={NotFound} />
            </Switch>
         </Router>
      );
   }
}

export default App;
