import React, { Component } from 'react';
import { Link, NavLink } from 'react-router-dom';
import Sticky from 'react-stickynode';

class CustomNavbar extends Component {
   render() {
      var { mClass, nClass, cClass, slogo, hbtnClass } = this.props;
      return (
         <Sticky top={0} innerZ={9999} activeClass='navbar_fixed'>
            <header className='header_area'>
               <nav className={`navbar navbar-expand-lg menu_one ${mClass}`}>
                  <div className={`container ${cClass}`}>
                     <Link className={`navbar-brand sticky_logo1`} to='/'>
                        <div className='pay_icon2'>
                           <div className='icon_shape'></div>
                           <img src={require('../img/home9/icon3.png')} alt='' />
                        </div>
                        <img src={require('../img/home9/icon3.png')} alt='' />
                     </Link>
                     <button
                        className='navbar-toggler collapsed'
                        type='button'
                        data-toggle='collapse'
                        data-target='#navbarSupportedContent'
                        aria-controls='navbarSupportedContent'
                        aria-expanded='false'
                        aria-label='Toggle navigation'>
                        <span className='menu_toggle'>
                           <span className='hamburger'>
                              <span></span>
                              <span></span>
                              <span></span>
                           </span>
                           <span className='hamburger-cross'>
                              <span></span>
                              <span></span>
                           </span>
                        </span>
                     </button>

                     <div className='collapse navbar-collapse' id='navbarSupportedContent'>
                        <ul className={`navbar-nav menu ml-auto ${nClass}`}>
                           {' '}
                           {/* <li className='nav-item'>
                              <NavLink to='/Bloglist' className='nav-link' title='Blog'>
                                 Blog List
                              </NavLink>
                           </li> */}
                           <li className='nav-item'>
                              <NavLink title='Pricing' className='nav-link' to='/Contact'>
                                 Contact
                              </NavLink>
                           </li>
                           <li className='nav-item'>
                              <NavLink exact title='Faq' className='nav-link' to='/Faq'>
                                 Faq
                              </NavLink>
                           </li>
                           <li className='nav-item'>
                                    <NavLink exact title='SignIn' className='nav-link' to='/SignIn'>
                                       Sign In
                                    </NavLink>
                                 </li>
                                 <li className='nav-item'>
                                    <NavLink exact title='SignUp' className='nav-link' to='/SignUp'>
                                       Sign Up
                                    </NavLink>
                                 </li>

                        </ul>
                        <a className={`btn_get btn_hover ${hbtnClass}`} href='#get-app'>
                           Get Started
                        </a>
                     </div>
                  </div>
               </nav>
            </header>
         </Sticky>
      );
   }
}

export default CustomNavbar;
